    position: relative;
}

img.smartphone {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate( -50%, -50% );
}

div.content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate( -50%, -50% );
    width: 250px;
    height: 402px;
}